<template>
  <div class="container-fluid">
    <div class="mt-3 ms-3">
      <CoolButton label="Go Back" route="/" />
    </div>

    <div class="text-center p-5">
      <h2>{{ className }}</h2>
      <div class="d-flex gap-5 justify-content-center class-details">
        <h5 class="level">
          Levels:
          <a class="text-nowrap d">{{ levelRange?.min }} - {{ levelRange?.max }}</a>
        </h5>
        <h5 class="type">
          Type:
          <a class="text-nowrap d">{{ type }}</a>
        </h5>
      </div>
    </div>

    <div v-if="tiers && tiers.length">
      <div class="d-flex flex-column flex-md-row justify-content-between gap-5">
        <div
          class="flex-grow-1"
          v-for="(tier, index) in tiers"
          :key="index"
        >
          <div class="col-12 text-center mb-4 p-3 rounded-4">
            <h4>
              Tier {{ index === 0 ? 'I' : index === 1 ? 'II' : 'III' }}:
              <a class="text-nowrap d">{{ tier.name }}</a>
            </h4>

            <h5 class="mt-3 mb-4 level-tier">
              Level: {{ tier.levelRange?.min }} - {{ tier.levelRange?.max }}
            </h5>

            <ul class="abilities-list">
              <li
                v-for="(ability, aIndex) in tier.abilities"
                :key="`${index}-${aIndex}`"
                class="ability-item"
                role="button"
                tabindex="0"
                @click="openAbility(ability)"
                @keydown.enter.prevent="openAbility(ability)"
                @keydown.space.prevent="openAbility(ability)"
              >
                <span class="ability-marker">
                  {{
                    tiers
                      .slice(0, index)
                      .reduce((acc, t) => acc + (t.abilities?.length || 0), 0)
                      + aIndex
                      + 1
                  }}
                </span>

                <div class="flex-grow-1 min-w-0 text-start">
                  <h5 class="ability-name mb-0">
                    <span class="ability-trigger">
                      {{
                        ability.name
                          ? ability.name.length > 20
                            ? ability.name.slice(0, 20) + '…'
                            : ability.name
                          : ''
                      }}
                      <span v-if="ability.name" class="ability-tooltip">
                        {{ ability.name }}
                      </span>
                    </span>
                  </h5>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <!-- Ability modal -->
    <div
      v-if="showAbilityModal"
      class="ability-modal-backdrop"
      @click.self="closeAbility"
    >
      <div
        class="ability-modal"
        role="dialog"
        aria-modal="true"
        :aria-label="selectedAbility?.name || 'Ability details'"
      >
        <div class="ability-modal-header">
          <h4 class="m-0">{{ selectedAbility?.name }}</h4>

          <button
            class="ability-modal-close"
            @click="closeAbility"
            aria-label="Close"
            type="button"
          >
            ✕
          </button>
        </div>

        <div class="ability-modal-body">
            <p class="mt-3">
                Type: {{ selectedAbility?.categories?.join(', ') || 'No type available.' }}
            </p>
            <p class="">
                {{ selectedAbility?.description || 'No description available.' }}
            </p>
        </div>

        <div class="ability-modal-footer">
          <button class="ability-modal-btn" type="button" @click="closeAbility">
            Close
          </button>
        </div>
      </div>
    </div>
    <h5 class="text-center">
        <span
            v-for="(rule, index) in rules"
            :key="index"
            class="rules"
        >
            {{ rule.name }} : {{ rule.text}}
            <span v-if="index < rules.length - 1"></span>
        </span>
    </h5>
  </div>
</template>

<script>
import CoolButton from '@/components/CoolButton.vue'

export default {
  name: 'SkillTree',
  components: { CoolButton },

  data() {
    return {
      className: this.$route.query.class,

      skills: null,
      loading: true,
      error: null,

      levelRange: null,
      type: null,
      tiers: null,
      rules: null,

      // Ability modal state
      showAbilityModal: false,
      selectedAbility: null
    }
  },

  watch: {
    skills() {
      if (!this.skills) return

      this.levelRange = this.skills.levelRange
      this.type = this.skills.archetype
      this.tiers = this.skills.tiers
      this.rules = this.skills.globalRules
    }
  },

  methods: {
    openAbility(ability) {
      this.selectedAbility = ability
      this.showAbilityModal = true
    },

    closeAbility() {
      this.showAbilityModal = false
      this.selectedAbility = null
    },

    onKeydown(e) {
      if (e.key === 'Escape' && this.showAbilityModal) {
        this.closeAbility()
      }
    }
  },

  async mounted() {
    window.addEventListener('keydown', this.onKeydown)

    try {
      const module = await import('@/assets/skills/skilltree.json')
      const trees = module.default.skillTrees || module.default || module

      this.skills = trees.find(
        t => t.class.toLowerCase() === this.className.toLowerCase()
      )

      if (!this.skills) {
        throw new Error('Skill tree not found')
      }
    } catch (err) {
      this.error = err
      console.error(err)
    } finally {
      this.loading = false
    }
  },

  beforeUnmount() {
    window.removeEventListener('keydown', this.onKeydown)
  }
}
</script>

