<template>
    <button class="btn" @click="changeRoute($props.route)">{{ $props.label }}</button>
</template>
<script>
export default {
    name: 'CoolButton',
    props: {
        label: {
            type: String,
            required: true
        },
        route: {
            required: true
        }
    },
    methods: {
        changeRoute(){
            this.$router.push(this.route);
        }
    }
}
</script>
<style>
    button.btn {
        background-color: #000000; /* Green */
        color: #ffc800 !important;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        cursor: pointer;
    }
    button.btn:hover {
        background-color: #271919; /* Darker green on hover */
    }
</style>